<template>
    <div>
        <img class="img-fluid pt-5 mt-5 w-25 mx-auto d-block" alt="logo tukarjual.com" src="../../assets/site-logo.webp">
        <h6 class="text-center mb-5">Daftar akun baru sekarang</h6>
        <form @submit.prevent="register">
            <div class="form-group">
                <div class="col">
                    <input required v-model="number_phone" type="tel" name="number_phone" placeholder="Masukkan nomor whatsapp" class="form-control form-control-lg bg-light">
                </div>
            </div>
            <div class="form-group">
                <div class="col">
                    <div class="d-flex justify-content-center">
                        <input :disabled="!disabledOTP" v-model="code" type="text" name="" placeholder="Masukkan OTP" class="form-control form-control-lg bg-light mr-2">
                        <button @click="send_code" v-if="!sended_code" :disabled="!valid_number_phone" class="btn col-5 float-right text-white tombol">Dapatkan kode</button>
                        <button v-else class="btn btn-outline-info col-5 float-right">{{timeout+' detik'}}</button>
                    </div>
                </div>
            </div>
            <div class="form-group">
                <div class="col">
                    <div class="input-group mb-2">
                        <input required autocomplete="new-password" name="new_password" v-model="password" :type="hidden_password ? 'password' : 'text'" placeholder="Masukkan kata sandi" class="form-control form-control-lg border-right-0">
                        <div class="input-group-append">
                            <div class="input-group-text bg-light">
                                <i v-if="hidden_password" @click="hidden_password=false" class="fa fa-eye-slash"></i>
                                <i v-else @click="hidden_password=true" class="fa fa-eye"></i>
                            </div>
                        </div>
                    </div>
                    <small class="text-muted">Kata sandi harus berisi minimal 6 karakter</small>
                </div>
            </div>
            <div class="col pt-3">
                <input type="submit" value="Daftar" class="btn btn-lg btn-block text-white tombol">
            </div>
        </form>
        <div class="col d-flex flex-row-reverse">
            <p class="mt-3">Sudah beisi akun? <router-link to="login" class="badge badge-info" style="cursor: pointer">Login Disini</router-link></p>
        </div>
        <modal-warning :image="$store.state.url_images+'modal/number_registered.png'" :ids="'number_phone_exists'" :message="{title: 'Pendaftaran Gagal', deskripsi: 'Nomor handphone yang anda masukkan sudah terdaftar'}"></modal-warning>
        <modal-warning :image="$store.state.url_images+'modal/wrong-otp.svg'" :ids="'wrong_otp'" :message="{title: 'OTP Salah', deskripsi: 'Kode OTP yang Pian masukkan salah'}"></modal-warning>
    </div>
</template>
<script>
import modalWarning from '@/components/modal/Failed'
import $ from 'jquery'
export default {
    name: "Register",
    data(){
        return {
            number_phone:"",
            last_number_phone:"",
            password:"",
            code:"",
            hidden_password:true,
            sended_code:false,
            disabledOTP:false,
            timeout:60,
            interval:null,
            loading_sending_code:false
        }
    },
    components:{
        modalWarning
    },
    computed:{
        valid_number_phone(){
            return this.number_phone.length>=10;
        },
        valid_password(){
            return this.password.length>=6;
        },
        valid_code(){
            return this.code.length==5;
        }
    },
    watch:{
        timeout(value){
            if(value==0){
                this.sended_code = false;
                clearInterval(this.interval)
                this.interval = null;
                this.timeout = 60;
            }
        }
    },
    methods:{
        send_code(){
            this.loading_sending_code = true;
            this.last_number_phone = this.number_phone;
            this.axios.get(this.$store.state.url_api + 'auth/check_phone_to_register/'+this.last_number_phone).then(response => {
                if(response.data.phone_ok){
                    this.axios.post(this.$store.state.url_api + 'auth/send-code', {
                        number_phone : this.number_phone
                    }).then(response => {
                        if(response.data.meta.success){
                            this.disabledOTP = true;
                            this.sended_code = true;
                            this.loading_sending_code = false;
                            var that = this;
                            this.interval = setInterval(function(){
                                that.timeout--;
                            },1000);
                        }
                    })
                }else{
                    $("#number_phone_exists").modal('show')
                }
            })
        },
        register(){
            if(this.valid_password&&this.valid_code){
                this.axios.post(this.$store.state.url_api + 'auth/register', {
                    number_phone : this.last_number_phone,
                    password : this.password,
                    code:this.code
                }).then(response => {
                    if(!response.data.meta.errors){
                        localStorage.setItem('token', response.data.token);
                        this.$store.dispatch('setIfLogin', true);
                        this.$store.dispatch('loadCart');
                        this.$router.push({name:"HomePage"});
                    }else{
                        $('#wrong_otp').modal('show')
                    }
                })
            }
        }
    }
}
</script>
<style scoped>
    input:disabled{
        background: rgb(199, 199, 199) !important;
        opacity: .6 !important;
    }
</style>